import React from 'react'
import Panel from 'muicss/lib/react/panel';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import nl2br from 'react-nl2br';

const ClgTicket = ({node}) => (
  <Panel>
  <Container fluid={true} >
  <Row >
    <Col md="12" className="mui--bg-primary mui--text-light">
    <div className="mui--pull-right mui--text-caption">{node.Date_de_d_but}</div>
    <div className="mui--text-subhead">{node.Etablissement_concern___Nom_complet}</div>
    </Col>
  </Row>
  <Row >
    <Col md="3">
       <small>{node.Produit__Produit}</small><br/>
    </Col>
    <Col md="4">
       <small>{node.Service__Nom}</small>
    </Col>
    <Col md="5">
       <small>{node.Sous_cat_gorie_de_service__Nom}</small><br/><br/>
    </Col>
  </Row>
  <Row >
    <Col md="2">
       <b><a href={'https://amigo.phm.education.gouv.fr/pages/UI.php?class=UserRequest&id='+node.R_f_rence+'&operation=details'} target="blank">{node.R_f_rence}</a></b>
    </Col>
    <Col md="8">

       <h3>{node.Objet_de_la_demande}</h3>
    </Col>
    <Col md="2" className={(node.Priorit_.includes("urgente") ? 'mui--bg-danger mui--text-caption' : 'mui--bg-primary-light mui--text-caption')} >
       Priorité {node.Priorit_} <br/>
       {node.Nombre_de_relances > 0 &&
       <small>Relances: {node.Nombre_de_relances} </small>}

    </Col>
  </Row>

  <Row >
    <Col md="2" className="mui--text-dark-secondary">
    <small>D:{node.Date_de_d_but}</small><br/>
    <small>M:{node.Derni_re_mise___jour}</small><br/><br/>
    <small>{node.Demandeur__Nom_complet}</small><br/><br/>
    <small>TEL COLLEGE:</small><br/> {node.Etablissement_concern___T_l_phone}<br/>
    <small>TEL RAPPEL: </small><br/> {node.T_l_phone_de_rappel}<br/>
    </Col>
    <Col md="8">
       {nl2br(node.Description)}
    </Col>
    <Col md="2" className="mui--text-accent">
    {node.Statut}<br/><br/>
    <small>AFFECTATION:</small><br/>
    <b><small>{node.Equipe__Nom}</small></b><br/>
    <small>{node.Agent__Nom_complet}</small>

    </Col>
  </Row>

  </Container>
  </Panel>
)

export default ClgTicket
